  height: 32px;
  fill: rgb(253, 146, 34);
}

.benefits__title {
  font-size: 18px;
  font-weight: 600;
  line-height: normal;
  color: rgb(255, 255, 255);
  letter-spacing: normal;
  margin-bottom: 18px;
}

.benefits__content {
