  color: rgba(255, 255, 255, 0.7);
}

.ingredients {
  width: 100%;
  padding: 128px 0 64px 0;
}

.ingredients__title {
  max-width: 411px;
  margin-bottom: 28px;
}

.ingredients__subtitle {
