.top-sellers__images-track {
  min-width: 100%;
  display: grid;
  grid-template-columns: repeat(3, minmax(230px, 368px));
  column-gap: 16px;
}

.top-sellers__img {
  width: 100%;
  height: 100%;
  aspect-ratio: 1/1.26;
  border-radius: 15px;
}

.top-sellers__content {
  width: 100%;
  display: flex;
  align-items: start;
  justify-content: space-between;
  flex-wrap: wrap;
  column-gap: 32px;
  row-gap: 20px;
}

.top-selers__text {
  max-width: 407px;
}

