  .header__nav {
    position: fixed;
    top: 0;
    right: 0;
    transition: all 0.8s;
    max-width: 290px;
    height: 100vh;
    transform: translateX(100%);
    padding: 156px 0 0 85px;
    background-color: rgb(17, 17, 17);
    z-index: 5;
  }

  .header__nav .nav__list {
    flex-direction: column;
    align-items: start;
  }

  .header__nav .nav__item:not(:last-child) {
    margin-bottom: 24px;
  }

  .header__nav .nav__link {
    color: rgb(255, 255, 255);
    transition: color 0.3s;
  }

  .header__nav .nav__link:hover {
    color: rgb(253, 146, 34);
  }

  .header__social {
    display: none;
  }

  .nav__social {
    position: absolute;
    left: 32px;
    bottom: 74px;
    width: 56px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .nav__social svg {
    width: 21px;
    height: 21px;
    stroke: rgb(255, 255, 255);
    stroke-width: 1.8;
    fill: none;
    transition: all 0.3s;
  }

  .nav__social-link:hover svg {
    stroke: rgb(253, 146, 34);
  }

  .burger {
    display: block;
  }

  .untranslate {
    transform: translateX(0);
  }

  .span-one-transform {
    top: 50%;
    transform: rotate(45deg);
    transition: all 0.3s;
    background-color: rgb(255, 255, 255);
  }

  .span-two-transform {
    transform: rotate(-45deg);
    transition: all 0.3s;
    background-color: rgb(255, 255, 255);
  }

  .span-three-transform {
    display: none;
  }

  .hero__inner {
